.row-header{
    margin:0px auto;
    padding:0px auto;
}

.row-content {
    margin:0px auto;
    padding: 50px 0px 50px 0px;
    border-bottom: 1px ridge;
    min-height:400px;
}

.footer{
    background-color:rgb(143, 97, 4);
    
    
    left: 0;
    bottom: 0;
    top:0;
    width: 100%;
    padding: 15px 0px 10px 0px; 
    margin-top:5px;
}
.jumbotron {
    padding:30px 20px 50px 30px;
    margin:0px auto;
    background: #292928 ;
    color:rgb(246, 166, 8);
}

address{
    font-size:80%;
    margin:0px;
    color:#0f0f0f;
}

.navbar-dark {
    background-color:rgb(143, 97, 4);
    color:aqua;
}

.page-enter {
    opacity: 0.01;
    transform: translateX(-100%);
}



for transition


.page-enter-active {
    opacity: 1;
    transform: translateX(0%);
    transition: all 300ms ease-in;
}

.page-exit {
    opacity: 1;
    transform: translateX(0%);
}

.page-exit-active {
    opacity: 0.01;
    transform: translateX(100%);
    transition: all 300ms ease-out;
}